<template>
  <div id="payemnts-table" class="form-group">
    <table class="table table-bordered table-striped">
      <thead>
      <tr>
        <th v-bind:key="item" v-for="item in columnHeads">
          {{item}}
        </th>
      </tr>
      </thead>
      <tr v-bind:key="item.paymentDate" v-for="item in currentPayments" class="form-control-static">
        <td>{{ (new Date(item.paymentDate)).toLocaleDateString("ru", options) }}</td>
        <td>{{ item.paymentAmount.toFixed(2) }} RUB</td>
      </tr>
    </table>
    <div class="form-group">
      <button class="btn" @click="movePages(-1)">Назад</button>
      <label>{{offset / limit + 1}} из {{pages}}</label>
      <button class="btn" @click="movePages(1)">Вперед</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PaymentsTable',
  data () {
    return {
      options: {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      },
      columnHeads: ['Дата платежа', 'Сумма платежа'],
      limit: 12,
      offset: 0,
      payments: this.value || []
    }
  },
  props: {
    value: Array
  },
  computed: {
    pages: function () {
      return Math.ceil(this.value.length / this.limit)
    },
    currentPayments: function () {
      let payments = []
      for (let i = this.offset; i < this.limit + this.offset && i < this.value.length; i++) {
        payments.push(this.value[i])
      }
      return payments
    }
  },
  methods: {
    movePages: function (amount) {
      let newOffset = this.offset + (amount * this.limit)
      if (newOffset >= 0 && newOffset < this.value.length) {
        this.offset = newOffset
      }
    }
  }
}
</script>

<style scoped>
  th {
    text-align: center;
  }
</style>



